import React, {useState} from "react";

const Contact = () => {
  const [data, setData] = useState({
    fullname: '',
    email: '',
    phone: '',
    msg: ''
  });

  const formSubmit = (e) => {
    e.preventDefault();
    alert(`Thank you ${data.fullname} for submitting the form. Your message ${data.msg} has been sent`);
  }

  const inputEvent = (event) => {
    const {name, value} = event.target;
    setData((prevData) => {
      return {
        ...prevData,
        [name] : value,
      }
    })
  }
  return(
    <>
        <div className="my-5">
          <h1 className="text-center">Get In touch!</h1>
        </div>
        <div className="container contact_div">
          <div className="row">
            <div className="col-md-6 col-10 mx-auto">
            <form onSubmit={formSubmit}>
            <div className="mb-3">
              <label for="exampleFormControlInput1" className="form-label">Full Name</label>
              <input type="text" className="form-control" id="exampleFormControlInput1"
              name="fullname"
              value={data.fullname}
              onChange={inputEvent}
              placeholder="Enter your Full Name" />
            </div>
            <div className="mb-3">
              <label for="exampleFormControlInput1" className="form-label">Email ID</label>
              <input type="email" className="form-control" id="exampleFormControlInput1"
              name="email"
              value={data.email}
              onChange={inputEvent}
              placeholder="name@example.com" />
            </div>
            <div className="mb-3">
              <label for="exampleFormControlInput1" className="form-label">Phone No.</label>
              <input type="number" className="form-control" id="exampleFormControlInput1"
              name="phone"
              value={data.phone}
              onChange={inputEvent}
              placeholder="10 digit Number only" />
            </div>
            <div className="mb-3">
              <label for="exampleFormControlTextarea1" className="form-label">Message</label>
              <textarea className="form-control" id="exampleFormControlTextarea1" rows="3"
              name="msg"
              value={data.msg}
              onChange={inputEvent}></textarea>
            </div>
            <div class="col-12">
              <button class="btn btn-outline-danger" type="submit">Submit form</button>
            </div>
            </form>  
            </div>
          </div>
        </div>
    </>
  );
}

export default Contact;